import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    activo: 0,
    backgroundimg: true
  },
  mutations: {
    setBackgroundimg(state, target) {
      state.backgroundimg = target;
    },
    ActualizarStado(state, id) {
      state.activo = id;
      console.log('State =', state.activo)
      return state.activo;
    }
  },
  actions: {
    cambiAct(state) {
      state.commit('cambiAct')

    }
  },
  modules: {

  }
})
