<template>
    <!-- @beforeLeave="beforeLeave"
    @enter="enter"
    @afterEnter="afterEnter" -->
  <transition
    appear
    :name="transitionName"
    :mode="transitionMode"
    :enter-active-class="transitionEnterActiveClass"

  >
    <slot />
  </transition>
</template>

<script>
const DEFAULT_TRANSITION = "fade";
const DEFAULT_TRANSITION_MODE = "out-in";

export default {
  name: "TransitionPage",
  data() {
    return {
      prevHeight: 0,
      transitionName: DEFAULT_TRANSITION,
      transitionMode: DEFAULT_TRANSITION_MODE,
      transitionEnterActiveClass: ""
    };
  },
  computed: {
    // overlayVisible(){
    //   return this.$store.state.overlayVisible
    // },
  },
  created() {
    this.$router.beforeEach((to, from, next) => {

      let transitionName =
        to.meta.transitionName ||
        from.meta.transitionName ||
        DEFAULT_TRANSITION;

      if (transitionName === "slide") {
        const toDepth = to.meta.index;
        const fromDepth = from.meta.index;
        transitionName = toDepth < fromDepth ? "slide-right" : "slide-left";
      }

      this.transitionMode = DEFAULT_TRANSITION_MODE;
      this.transitionEnterActiveClass = `${transitionName}-enter-active`;

      // if (to.meta.transitionName === "zoom") {
      //   // this.transitionMode = 'in-out';
      //   this.transitionEnterActiveClass = 'zoom-enter-active';
      //   // document.body.style.overflow = 'hidden';
      // }

      // if (from.meta.transitionName === "zoom") {
      //   // this.transitionMode = null;
      //   this.transitionEnterActiveClass = null;
      //   // document.body.style.overflow = null;
      // }

      // if (!to.meta.navBar) {
      //   this.$store.commit("setNavBar", false); 
      // } else {
      //   this.$store.commit("setNavBar", true); 
      // }

      // if (!to.meta.backgroundVideo) {
      //   this.$store.commit("setBackgroundVideo", false); 
      //   // this.$store.dispatch("disableBackgroundVideo"); 
      // } else {
      //   this.$store.commit("setBackgroundVideo", true); 
      //   // this.$store.dispatch("setBackgroundVideo"); 
      // }

      this.transitionName = transitionName;
      next();
    });
  },
  methods: {
    // beforeLeave(element) {
      //   this.prevHeight = getComputedStyle(element).height;
      // Clean last screen Vars
      // this.$store.commit("setLoading", false);
      // this.$store.commit('setNavBar', false);
    // },
    // enter(element) {
      //   const { height } = getComputedStyle(element);
      //   element.style.height = this.prevHeight;
      //   setTimeout(() => {
      //     element.style.height = height;
      //   });
    // },
    // afterEnter(element) {
      // window.scrollTo(0, 0);
      //   element.style.height = 'auto';
    // }
  }
};
</script>

<style lang="scss">
.fade-enter-active,
.fade-leave-active {
  transition-duration: 0.5s;
  transition-property: height, opacity;
  transition-timing-function: ease;
  overflow: hidden;
}

.fade-enter,
.fade-leave-active {
  opacity: 0;
}

.slide-left-enter-active,
.slide-left-leave-active,
.slide-right-enter-active,
.slide-right-leave-active {
  transition-duration: 0.3s;
  transition-property: height, opacity, transform;
  transition-timing-function: cubic-bezier(0.55, 0, 0.1, 1);
  overflow: hidden;
}

.slide-left-enter,
.slide-right-leave-active {
  transform: translate(100vw, 0);
}

.slide-left-leave-active,
.slide-right-enter {
  transform: translate(-100vw, 0);
}

.zoom-enter-active,
.zoom-leave-active {
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-name: zoom;
}

.zoom-leave-active {
  animation-direction: reverse;
}

.zoomOut-SlideIn-enter-active {
  transition-duration: 0.5s;
  transition-property: height, opacity, transform;
  transition-timing-function: cubic-bezier(0.55, 0, 0.1, 1);
}

.zoomOut-SlideIn-enter {
  transform: translate(0, 100vh);
}

.zoomOut-SlideIn-leave-active {
  animation-duration: 0.5s;
